import { Fragment, useEffect, useState } from "react";
import "./EditUser.css";
import { useAlert } from "react-alert";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate, useParams } from "react-router-dom";
import { adminAuth, userAuth } from "../../../../util/userAuth";
import MetaData from "../../../layout/MetaData/MetaData";
import SideBar from "../Dashboard/SideBar/SideBar";
import {
  updateUser,
  clearErrors,
  getUserDetails,
} from "../../../../redux/actions/userAction";
import { IoMdPerson } from "react-icons/io";
import { FiMail } from "react-icons/fi";
import { UPDATE_USER_RESET } from "../../../../redux/constants/userConstant";
import { Typography } from "@material-ui/core";
import { MdAccountTree } from "react-icons/md";

const EditUser = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const alert = useAlert();
  const { id } = useParams();

  const { isUpdated, error, loading } = useSelector(
    (state) => state.updateUser
  );
  const { isAuthenticated, user: authUser } = useSelector(
    (state) => state.user
  );

  const { user: getUser } = useSelector(
    (state) => state.getUser
  );

  const [user, setUser] = useState({
    name: "",
    email: "",
    role: "",
  });

  const dataOnChange = (e) => {
    setUser({ ...user, [e.target.name]: e.target.value });
  };
  const updateUserSubmit = (e) => {
    e.preventDefault();
    const myForm = new FormData();
    myForm.set("name", user.name);
    myForm.set("email", user.email);
    myForm.set("role", user.role);

    dispatch(updateUser(id, myForm));
  };

  useEffect(() => {
    userAuth(isAuthenticated, navigate);
    if (authUser) adminAuth(true, authUser, navigate, alert);

    if (getUser) {
      setUser({
        name: getUser.name,
        email: getUser.email,
        role: getUser.role,
      });
    }

    if (error) {
      alert.error(error);
      dispatch(clearErrors());
    }

    if (isUpdated) {
      alert.success("User Updated Successfully");
      dispatch({ type: UPDATE_USER_RESET });
      dispatch(getUserDetails(id));
      navigate("/admin/users");
    }
    dispatch(getUserDetails(id));
  }, [
    isAuthenticated,
    authUser,
    navigate,
    alert,
    dispatch,
    error,
    isUpdated,
    getUser,
    id,
  ]);

  return (
    <Fragment>
      <MetaData title={`Update Product`} />

      <div className="dashboard">
        <SideBar />

        <div className="newProductContainer">
          <form className="signupForm" onSubmit={updateUserSubmit}>
            <Typography>Edit User Role</Typography>
            <div className="signupName">
              <IoMdPerson />
              <input
                type="text"
                placeholder="Name"
                required
                name="name"
                value={user.name}
                onChange={dataOnChange}
              />
            </div>
            <div className="signupEmail">
              <FiMail />
              <input
                type="email"
                placeholder="Email"
                required
                name="email"
                value={user.email}
                onChange={dataOnChange}
              />
            </div>

            <div>
              <MdAccountTree />
              <select
                required
                name="role"
                onChange={dataOnChange}
              >
                <option value="">Select Category</option>

                <option value="admin">admin</option>

                <option value="user">user</option>
              </select>
            </div>
            <input
              type="submit"
              value="Update"
              className="signupBtn"
              disabled={
                loading ? true : false || user.role === "" ? true : false
              }
            />
          </form>
        </div>
      </div>
    </Fragment>
  );
};

export default EditUser;
